<template>
    <div class="container wrapper">
        <div class="split">
            <div id="answers" v-for="(answer, index) in state.summaryAnswers" :key="index">
                <h3>{{ state.answerTitles[answer.id] }}</h3>
                <img src="../assets/pen.png" width="15" @click="editAnswer(index)">
                <p>{{ answer.comment }}</p>
            </div>
            <SummaryAnswer
                :class="{ hidden: state.progress === 4 }"
                :title="state.answerTitles[state.progress]"
                :progress="state.progress"
                :comment="state.answerComment"
                @answer="saveAnswer" />
            <div v-if="state.progress === 4" id="ready">
                <h2>Hienoa! Jos olet valmis, jatka seuraavaan vaiheeseen.<br> Voit halutessasi vielä muokata antamiasi vastauksia.</h2>
                <button @click="finishPhase">Seuraavaan vaiheeseen</button>
            </div>
        </div>
        <div class="split">
            <div id="trigendaBoard">
                <div class="cardDiv">
                    <div id="triangleImage">
                        <h1 id="descBottom">{{ state.goalCard.description }}</h1>
                    </div>
                </div>
                <div id="secondCardRow">
                    <div class="cardDiv">
                        <div id="triangleImage">
                            <h1 id="descBottom">{{ state.assetOneCard.description }}</h1>
                        </div>
                    </div>
                    <div class="cardDiv">
                        <div id="triangleImage">
                            <h1 id="descBottom">{{ state.challengeCard.description }}</h1>
                        </div>
                    </div>
                    <div class="cardDiv">
                        <div id="triangleImage">
                            <h1 id="descBottom">{{ state.assetTwoCard.description }}</h1>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Card from './Card.vue';
import { reactive } from 'vue';
import SummaryAnswer from './SummaryAnswer.vue';

export default {
    name: "Summary",
    props: {
        msg: String,
    },
    components: {
        Card,
        SummaryAnswer
    },
    setup(props, { emit }) {
        const state = reactive({
            // Index of the current answer
            progress: 0,
            // Title for SummaryAnswer component, array index based on progress
            answerTitles: ['Keskustelun pääsisällöt, mistä asioista keskusteltiin:',
                'Keskustelussa nousseet tavoitteet ja keinot niiden saavuttamiseksi:',
                'Keskustelun oivallukset:',
                'Vapaa sana:'
            ],
            // Prop to SummaryAnswer, sets the comment for the textbox
            answerComment: '',

            /**
             * Answer objects in the array, Id property is based on progress and comment is
             * based on given answer in the SummaryAnswer component
             */
            summaryAnswers: [],
            // Cards based on placement in Phase 2
            goalCard: null,
            challengeCard: null,
            assetOneCard: null,
            assetTwoCard: null
        });

        const cardArray = JSON.parse(localStorage.getItem("cardArray"));

        state.goalCard = cardArray.find(card => card.phase2selection === 'Tavoite');
        state.challengeCard = cardArray.find(card => card.phase2selection === 'Haaste');
        state.assetOneCard = cardArray.find(card => card.phase2selection === 'Voimavara1');
        state.assetTwoCard = cardArray.find(card => card.phase2selection === 'Voimavara2');


        const saveAnswer = (answer) => {
            const oldAnswerIndex = state.summaryAnswers.findIndex(answer => answer.id === state.progress);

            /**
             * Check if there is already given comment on current answer. This is possible if user is
             * editing an already existing answer.
             * if old comment exists, replace it. Otherwise push a new answer to summaryAnswers
             */
            if (oldAnswerIndex >= 0)
                state.summaryAnswers[oldAnswerIndex].comment = answer;
            else
                state.summaryAnswers.push({ id: state.progress, comment: answer });

            localStorage.setItem('summaryAnswers', JSON.stringify(state.summaryAnswers));

            /**
             * Increases progress as long as there is not given answer in progress index.
             * This way we get always to the latest not-yet-given answer after editing an old comment.
             * Or to end of the Summary if all answers are already given
             */
            while (state.summaryAnswers[state.progress])
                state.progress++;

            state.answerComment = '';
        }

        const editAnswer = (index) => {
            state.progress = index;
            state.answerComment = state.summaryAnswers[index].comment;
        }

        const finishPhase = () => {
            emit('phasecomplete');
        }

        return { state, saveAnswer, editAnswer, finishPhase };
    },
    emits: ['phasecomplete'],
};

</script>

