<template>
    <nav id="navbar">
        <div class="tringo-toggle">
            <div class="info" @click="toggleInfo"></div>
        </div>
        <div class="logo">
           <router-link to="/"><img src="../assets/DigiLogo.png" alt="TrigendaDigi"></router-link>
        </div>
        <div class="links">
            <router-link to="/"><p></p></router-link>
        </div>
    </nav>
</template>

<script>
import { reactive } from 'vue';

export default {
    name: 'Navbar',
    setup(props, { emit }) {
        const state = reactive({ showInfo: true });

        const toggleInfo = () => {
            if (state.showInfo === true)
                state.showInfo = false;
            else
                state.showInfo = true;

            emit('toggleInfo', state.showInfo);
        }

        return { state, toggleInfo };
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
    font-size: 1.4em;
}

nav {
    display: flex;
    flex: auto auto auto;
    justify-content: space-between;
    background-color: #444;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    height: 8%;
    overflow: hidden;
    z-index: 100;
    border-bottom: 0.7vh solid #76B2BA;
}

nav > * {
    flex: auto auto auto;
}

.links {
    position: relative;
    left: 0;
    padding: 0 20px;
}

.tringo-toggle {
    width: 10%;
}
.links {
    width: 10%;
    padding: 0px;
}
.links p {
    font-size: 2vh;
    margin-top: 2.8vh;
}

.logo {
    height: 100%;
}

.logo img {
    height: 70%;
    margin-top: 7.8%;
}

nav > a {
    color: white;
    text-decoration: none;
    padding: 1em 3em;
    margin: 0 0.1em;
    width: 10em;
    font-size: 1em;
}

nav a:hover {
    color: #48a3a7;
}

a {
    color: white;
}

.info {
    background: url("../assets/info-icon.png") no-repeat;
    height: 70%;
    margin-left: 15px;
    margin-top: 2vh;
    min-width: 35px;
}
.info:hover {
    cursor: pointer;
}

</style>
